  <template>
  <div>
    <p style="margin: 20px 0">
      <el-button @click="allSelection"></el-button>
      <el-checkbox style="margin-left: 30px" v-model="checked"
        >||</el-checkbox
      >
    </p>
    <u-table
  
      ref="plTable"
      :height="height"
      row-key="id"
      use-virtual
      :big-data-checkbox="checked"
      showBodyOverflow="title"
      showHeaderOverflow="title"
      :row-height="rowHeight"
      @select-all="selectall"
      @select="select"
      @sort-change="sortChange"
      border
    >
      <u-table-column
        type="selection"
        width="55"
       
        fixed="left"
        align="center"
      />

      <u-table-column
        label=""
        width="230"
        prop="taskName"
        fixed="left"
        :sortable="'custom'"
      >
        <template slot-scope="data">
          <span class="ellipsisFlag">
            <span v-if="data.row.type == '2'">
              {{ data.row.taskName }}
            </span>
            <span v-else>
              {{ chinesechecked ? data.row.chinesename : data.row.taskName }}
            </span>
          </span>
        </template>
      </u-table-column>

      <u-table-column
        label=""
        prop="node_name"
        width="90"
        :sortable="'custom'"
      >
      </u-table-column>
      <u-table-column
        label=""
        prop="forecast"
        align="center"
        width="90"
        :sortable="'custom'"
      >
        <template slot-scope="data">
          <span
            @click="showEditer(data.row, 'forecast', $event)"
            class="ellipsisFlag"
            style="font-size: 14px"
            >{{ data.row.forecast }}</span
          >
        </template>
      </u-table-column>

      <u-table-column
        label=""
        prop="workingHours"
        align="center"
        width="90"
        :sortable="'custom'"
      >
        <template slot-scope="data">
          <span class="ellipsisFlag" style="font-size: 14px">{{
            data.row.workingHours
          }}</span>
        </template>
      </u-table-column>
      <u-table-column prop="producername" label="" width="200">
        <template slot-scope="data">
          <span
            v-if="data.row.producername.length == 0"
           
            class="ellipsisFlag"
          >
            --
          </span>
          <span
            v-else
            v-for="(item, index) in data.row.producername"
            :key="index"
           
            class="ellipsisFlag"
          >
            {{ item.name }}
          </span>
        </template>
      </u-table-column>
      <u-table-column prop="company_name" label="" width="200">
        <template slot-scope="data">
          <span class="ellipsisFlag">
            {{ data.row.company_name }}
          </span>
        </template>
      </u-table-column>
      <u-table-column prop="base" label="" width="200">
      </u-table-column>

      <u-table-column label="" align="center" fixed="right">
        <template slot-scope="data">
          <div
            :style="data.row.nodeState.style"
            style="font-size: 12px"
          
          >
            {{ data.row.nodeState.label }}
          </div>
        </template>
      </u-table-column>
      <u-table-column
        label=""
        align="center"
        width="50"
        fixed="right"
        
      >
        <template slot-scope="data">
          <div
            v-if="data.row.is_del != '1'"
            class="delPlanTaskStyle"
           
          >
            <i class="el-icon-error"></i>
            <!-- {{ data.row.nodeState.label }} -->
          </div>
        </template>
      </u-table-column>
    </u-table>
    <div style="margin-top: 20px">
      <el-button @click="toggleSelection([datas[0], datas[2]])"
        ></el-button
      >
      <el-button @click="toggleSelection([datas[3]], '')"
        ></el-button
      >
      <el-button @click="partRowSelections(true)">500</el-button>
      <el-button @click="partRowSelections(false)"
        >500</el-button
      >
      <el-button @click="toggleSelection()"></el-button>
      <el-button @click="getCheckboxRecords()"></el-button>
    </div>
  </div>
</template>

  <script>
  import data from '../../static/data.json'
export default {
  data() {
    return {
      chinesechecked:true,
      tableData:data,
      checked: true,
      height: 619,
      rowHeight: 50,
      columns: Array.from({ length: 10 }, (_, idx) => ({
        prop: "address",
        id: idx,
        label: "" + idx,
        width: 200,
      })),
    };
  },
  mounted() {
    console.log(this.tableData);

    // this.datas = this.tableData;
    // 
    this.$refs.plTable.reloadData(this.tableData);
  },
  methods: {
    sortChange(prop, order){
      console.log(prop);
      console.log(order);
    },
    select(selection, row){
      console.log(selection);
      console.log(row);
      console.log('');
    },
    selectall(val) {
      console.log("");
      console.log(val);
      // console.log(JSON.stringify(val));
    },
    selectable(row, index) {
      if (index === 1) {
        return false;
      } else {
        return true;
      }
    },
    allSelection() {
      this.$refs.plTable.toggleAllSelection();
    },
    clearSelection() {
      this.$refs.plTable.clearSelection();
    },
    // 
    partRowSelections(state) {
      // 500
      let data = this.datas.slice(0, 500);
      // datastate
      this.$refs.plTable.partRowSelections(data, state);
    },
    // ,partRowSelections
    toggleSelection(rows, type) {
      if (rows) {
        rows.forEach((row) => {
          //  element
          // this.$refs.plTable.toggleRowSelection(row); // element
          // 

          // 
          if (type === "") {
            // toggleRowSelectionapi
            this.$refs.plTable.toggleRowSelection([
              {
                row: row,
                selected: true,
              },
            ]);
            // 
          } else {
            this.$refs.plTable.toggleRowSelection([
              {
                row: row,
              },
            ]);
          }
        });
      } else {
        this.$refs.plTable.clearSelection();
      }
    },
    // 
    getCheckboxRecords() {
      console.log(this.$refs.plTable.getCheckboxRecords());
    },
  },
};
</script>


